import { Component, Host, Prop, State, Watch, h } from '@stencil/core';
import { isEmpty } from '@springbokagency/stencil-base';
import { IconName } from '../../../components';
import { ValidatorEntry, ValidatorNames } from '../../../validators/validators.types';
import { InputType } from './form-input.types';

/**
 * The form input component.
 */
@Component({
  tag: 'bs-form-input',
  styleUrl: 'form-input.scss',
  shadow: true,
})
export class BsFormInput {
  private formFieldRef: HTMLBsFormFieldElement;

  /**
   * Wether or not the input field is filled
   */
  @State() filled: boolean = false;

  /**
   * Wether or not the input field is focused
   */
  @State() focused: boolean = false;

  /**
   * Wether or not the input field is invalid
   */
  @State() invalid: boolean = false;

  /**
   * The unique name to identify the form element
   */
  @Prop() name!: string;

  /**
   * The unique name to identify the form element
   */
  @Prop() type: InputType = InputType.Text;

  /**
   * The label describing the form field
   */
  @Prop() label!: string;

  /**
   * The value of the form field
   */
  @Prop() value?: string = '';

  @Watch('value')
  validateValue(newValue: string) {
    const isBlank = typeof newValue !== 'string' || newValue === '';

    this.filled = !isBlank;
  }

  /**
   * Wether or not the input field is required
   */
  @Prop() required?: boolean = false;

  /**
   * Wether or not the input field is required
   */
  @Prop() min?: string;

  /**
   * Wether or not the input field is required
   */
  @Prop() max?: string;

  /**
   * The error message to be displayed when the required field remains empty
   */
  @Prop() requiredMessage: string;

  /**
   * The error message to be displayed when the field has an invalid input
   */
  @Prop() invalidMessage?: string;

  /**
   * The icon to be displayed on the right hand side of the form field
   */
  @Prop() icon?: IconName;

  /**
   * The default amount of rows the textarea has
   */
  @Prop() rows?: number;

  /**
   * Wether or not the textarea is resizeable by the user
   */
  @Prop() resizeable?: boolean;

  private inputEl: HTMLInputElement;

  private focus = () => {
    this.focused = true;

    if (this.inputEl !== undefined && this.placeholderInputTypes.includes(this.type)) {
      this.inputEl.showPicker();
    }
  };

  private focusout = (e: FocusEvent) => {
    this.value = (e.target as HTMLInputElement | HTMLTextAreaElement).value;
    this.formFieldRef.value = this.value;
    this.focused = false;
  };

  private getValidator = (): string | ValidatorEntry => {
    if (this.type === InputType.Email) {
      return ValidatorNames.Email;
    }

    if (!isEmpty(this.min) || !isEmpty(this.max)) {
      if (this.charInputTypes.includes(this.type as InputType)) {
        return { name: ValidatorNames.Length, options: { min: this.min, max: this.max } };
      }

      return { name: ValidatorNames.MinMax, options: { min: this.min, max: this.max } };
    }

    return this.type;
  };

  private charInputTypes = [
    InputType.Text,
    InputType.Email,
    InputType.Password,
    InputType.Tel,
    InputType.Textarea,
  ];

  private placeholderInputTypes = [InputType.Date, InputType.DateTime, InputType.Time];

  render() {
    const minMaxArg = this.charInputTypes.includes(this.type as InputType)
      ? { minlength: this.min, maxlength: this.max }
      : { min: this.min, max: this.max };

    return (
      <Host>
        <bs-form-field
          ref={(el) => {
            this.formFieldRef = el;
          }}
          value={this.value}
          name={this.name}
          hidden={this.type === 'hidden'}
          focused={this.placeholderInputTypes.includes(this.type) ? true : this.focused}
          filled={this.filled}
          invalid={this.invalid}
          required={this.required}
          invalidMessage={this.invalidMessage}
          requiredMessage={this.requiredMessage}
          label={this.label}
          icon={this.icon}
          validator={this.getValidator()}
          onClick={this.focus}
        >
          {this.type === 'textarea' ? (
            <textarea
              rows={this.rows}
              slot="form-element"
              class={{ 'form-input': true, 'is-resizeable': this.resizeable }}
              required={this.required}
              id={this.name}
              minlength={this.min}
              maxlength={this.max}
              value={this.value}
              name={this.name}
              onFocus={this.focus}
              onFocusout={this.focusout}
            ></textarea>
          ) : (
            <input
              ref={(el) => {
                this.inputEl = el;
              }}
              slot="form-element"
              class="form-input"
              required={this.required}
              id={this.name}
              type={this.type}
              {...minMaxArg}
              value={this.value}
              name={this.name}
              onFocus={this.focus}
              onFocusout={this.focusout}
            />
          )}
          <label slot="form-label" class="form-label" htmlFor={this.name} title={this.label}>
            {this.label}
            {this.required ? <span aria-hidden="true">&nbsp;*</span> : ''}
          </label>
        </bs-form-field>
      </Host>
    );
  }
}
